import axios from "axios";
import qs from "qs";
import {
  Toast
} from "vant";
import {
  getLang
} from "./i18n"
import md5 from "md5";
// import {
//   guid
// } from "@/storage";

import {
  config as baseConfig
} from './config'

const http = axios.create();
http.defaults.withCredentials = false;
http.defaults.headers.post["Content-Type"] =
  "application/x-www-form-urlencoded";
http.interceptors.request.use(
  config => {
    if (config.method === "post") {
      if (config.data && config.data.constructor === FormData) {
        return config;
      }
      if (!config.data) {
        config.data = {};
      }
      config.data.language = getLang().toLowerCase()
      config.data.ust = Math.floor(new Date().getTime() / 1000) + '';
      let _data = config.data;
      Object.keys(_data)
        .sort()
        .forEach(function (key) {
          config.data[key] = _data[key];
        });
      let apiKey = baseConfig.apiKey;
      // console.log(JSON.stringify(config.data) + apiKey);
      config.data.sign = md5(JSON.stringify(config.data) + apiKey);
      // console.log(config.data.sign)
      config.data = qs.stringify(config.data);
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

http.interceptors.response.use(
  response => {
    let data = response.data;
    if (typeof data === "object" && data.status !== 1) {
      if (data.info === 10001) {
        // guid传了但是提示未登录
        // guid.set('')
        return window.location.reload()
      }
      if (data.info) {
        Toast(data.info);
      }
      return Promise.reject(response.data);
    }
    return response.data;
  },
  error => {
    return Promise.reject(error);
  }
);
export default http;
